.presentation__block {
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-desktop-2.png");
  background-repeat: no-repeat;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-size: 320px 302px;
  width: auto;
  /*width: 320px;*/
  height: 302px;
  margin-bottom: 20px;

  @media (min-width: 1200px) {
    order: 2;
  }
}

.presentation__block-title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
  margin: 0;
  padding-bottom: 20px;
}

.presentation__block-text {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  margin: 0;
}

.presentation__wrapper {
  @media (min-width: 1200px) {
    display: flex;
    flex-direction: row;
  }
}
